<template>
	<view>
		<view class="topTabBar">
			<view class="grid" v-for="(item,tbIndex) in typeList" :key="tbIndex" @tap="showType(tbIndex)">
				<view class="text" :class="[tbIndex==tabbarIndex?'on':'']">{{item}}</view>
			</view>
		</view>
		<view class="container">
			<!-- #ifdef MP-WEIXIN -->
				<view class="h70"></view>
			<!-- #endif -->
			<!-- #ifdef H5 -->    
				<view class="h70"></view>
			<!-- #endif -->

			
			<view class="tow-list">
				<view class="item" v-for="item in list" :key="item.id" @tap="toGoodNightStoryDetail(item)">   <!-- aspectFill -->
					<image mode="aspectFill" :src="item.img" style="width: 345rpx; height: 345rpx;"></image>
					<view class="name">{{ item.name }}</view>
					<view class="time">上传时间{{ item.time }}</view>
				</view>
			</view>
			<view class="clear h40"></view>
		</view>
	</view>
</template>

<script>
	import common from "@/js/common.js";
	import config from "@/js/config.js";
	import util from "@/js/util.js";
	let statusBarHeight = uni.getSystemInfoSync().statusBarHeight; // 状态栏 高度
	let that = null;
	export default {
		data() {
			return {
				url:"http://admin.8630.net/WYB/UploadFile/headerTopbg.png",
				imageRootUrl:config.imageRootUrl,//图片地址
				statusBarHeight,// 状态栏 高度
				head_is_fixed:0,
				tabbarIndex: 0,
				typeList: ['看过', '喜欢', '收藏', '付费故事'], //选项卡
				list: [
					{id: 0,img: config.imageRootUrl + 'image/common/a7.jpg',name: '晚安故事1晚安故事1晚安故事1',time: '2023年12月12日'},
					{id: 0,img: config.imageRootUrl + 'image/common/a8.jpg',name: '晚安故事2',time: '2023年12月12日'},
					{id: 0,img: config.imageRootUrl + 'image/common/a11.jpg',name: '晚安故事3',time: '2023年12月12日'},
					{id: 0,img: config.imageRootUrl + 'image/common/a12.jpg',name: '晚安故事4',time: '2023年12月12日'},
					{id: 0,img: config.imageRootUrl + 'image/common/a17.jpg',name: '晚安故事5',time: '2023年12月12日'},
					{id: 0,img: config.imageRootUrl + 'image/common/a18.jpg',name: '晚安故事6',time: '2023年12月12日'},
				],
			};
		},
		onReady() {
			
		},
		onShow() {
			
		},
		onPageScroll(e) {
		    that.head_is_fixed = e.scrollTop > 0 ? 1 : 0
		},
		onLoad() {
			that = this;
			that.bindData(); //
		},
		methods: {
			bindData(){
				
			},
			showType(tbIndex) { //状态导航栏切换
				this.tabbarIndex = tbIndex;
				that.bindData();
			},
			clickToBack(){
				uni.navigateBack();
			},
			toGoodNightStoryDetail(){//跳转晚安故事详情
				setTimeout(() => {
					uni.navigateTo({
						url: 'goodNightStoryDetail'
					});
				}, 200);
			},
		},
	};
</script>

<style lang="scss">
	@import url("/css/common.css");
	page {
		background: #5EAC59;
	}
	.container{
		width:100%; 
		margin-left:0%;
	}
	.topTabBar {
		border-radius: 10rpx;
		width: 100%;
		position: fixed;
		
		
		z-index: 10;
		background-color: #FACC3F;
		height: 80rpx;
		display: flex;
		justify-content: space-around;
		// padding: 20rpx;
	
		.grid {
			width: 20%;
			height: 80upx;
			display: flex;
			justify-content: center;
			align-items: center;
			color: #444;
			font-size: 28upx;
	
			.text {
				height: 76upx;
				display: flex;
				align-items: center;
	
				&.on {
					color: #53D776;
					border-bottom: solid 4upx #53D776;
				}
			}
	
		}
	}
	.tow-list {
		.item {
			width: 345rpx;
			margin-left: 20rpx;
			margin-top: 20rpx;
			display: block;
			float: left;
			border-radius: 20upx;
			background-color: #fff;
			box-shadow: 0upx 5upx 25upx rgba(0, 0, 0, 0.1);
