				-webkit-line-clamp: 2;
				text-align: justify;
				overflow: hidden;
				font-size: 28upx;
				white-space: nowrap;
				text-overflow: ellipsis;
			}
			.time {
				width: 92%;
				padding: 10upx 4%;
				display: -webkit-box;
				-webkit-box-orient: vertical;
				-webkit-line-clamp: 2;
				text-align: justify;
				overflow: hidden;
				font-size: 30upx;
				white-space: nowrap;
				text-overflow: ellipsis;
				color:#999999;
				font-size: 24rpx;
			}
		}
	}
		
</style>